:root{
  --clr-bg:hsla(263, 62%, 55%, 0.938);
  --clr-bg2: indigo;
}

*,
::after,
::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background:var(--clr-bg);
  color:rgb(209,209,209);
  line-height: 1.5;
  font-size:0.875rem;
}

a{
  text-decoration: none;
}

h1,
h2,
h3,
h4{
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}

.section {
  width:90vm;
  margin:0 auto;
  max-width: 1100px;
}

@media screen and (max-width: 992px){
  .section {
    width:95vm;
  }
}

main {
  min-height: 100vh;
  display:flex;
  justify-content: center;
  align-items: center;
}

.container {
  width:90vw;
  margin:5rem 0;
  max-width:450px;
  background:var(--clr-bg2);
  padding:1.5rem 2rem;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  border-radius: 0.25rem;
}

.container h3 {
  font-weight: normal;
  text-transform: none;
  margin-bottom: 2rem;
}
.person {
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 0.75rem;
  margin-bottom: 1.5rem;
  align-items: center;
}
.person img {
  width: 75px;
  height: 75px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}
.person h4 {
  margin-bottom: 0.35rem;
}
.person p {
  margin-bottom: 0;
}
.container button {
  color: #fff;
  display: block;
  width: 100%;
  border-color: transparent;
  background: var(--clr-bg);
  margin: 2rem auto 0 auto;
  text-transform: capitalize;
  font-size: 1.2rem;
  padding: 0.5rem 0;
  outline: 1px solid rgba(242, 138, 178, 0.8);
  cursor: pointer;
}